@-webkit-keyframes atRotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes atRotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.at-toast-overlay {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  opacity: 0;
}

.at-toast .toast-body {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 1090;
  padding: 0.512rem 0.68267rem;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 0.17067rem;
  min-width: 5.46133rem;
}

.at-toast .toast-body-content__icon {
  line-height: 0;
  text-align: center;
}

.at-toast .toast-body-content__img {
  line-height: 0;
  text-align: center;
}

.at-toast .toast-body-content__img-item {
  width: 2.56rem;
  height: 2.56rem;
}

.at-toast .toast-body-content__info {
  margin-top: 0.384rem;
  color: #FFF;
  line-height: 1.5;
  font-size: 0.59733rem;
  text-align: center;
}

.at-toast .toast-body--text {
  padding: 0.256rem 0.512rem;
  line-height: 1.5;
  min-width: initial;
}

.at-toast .toast-body--text .toast-body-content__info {
  margin-top: 0;
}

.at-toast .toast-body.at-toast-body--custom-image,
.at-toast .toast-body.at-toast-body--success,
.at-toast .toast-body.at-toast-body--error,
.at-toast .toast-body.at-toast-body--loading {
  padding-bottom: 0.36267rem;
}

.at-toast-body--loading .toast-body-content__img-item {
  -webkit-animation: atRotate 1350ms linear infinite;
          animation: atRotate 1350ms linear infinite;
}

